import React, { useState, useEffect } from "react"
import PaginationPack from "components/PaginationPack"
import { connect, useSelector } from "react-redux"
//Components
import {
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  Row,
  Col,
  Spinner
} from "reactstrap"
import "./style.css"
import { benefitsRequest } from "./redux/actions"
import ReadMore from "components/ReadMore"
function Benefits(props) {
  const { requesting, resourceData, resourcesRequest, benefits } = props
  // console.log("benefits", benefits.results)
  useEffect(() => {
    props.benefitsRequest(1)
    // resourcesRequest(1)
  }, [])
  const handlePageChange = page => {
    props.benefitsRequest(page)
  }

  return (
    <>
      <div className="content">
        <Row>
          <Col md="12">
            <Card className="card-plain">
              <CardHeader>
                <div style={{ display: "flex", alignItems: "center" }}>
                  <CardTitle
                    tag="h4"
                    style={{
                      fontFamily: "Libre Caslon Text",
                      fontStyle: "normal",
                      fontWeight: "700",
                      fontSize: "22px",
                      lineHeight: "27px",
                      color: "#CACACA"
                    }}
                  >
                    Benefits
                  </CardTitle>
                </div>
              </CardHeader>
              <CardBody>
                <Row style={{}} className="p-0 m-0">
                  {requesting ? (
                    <div
                      style={{
                        position: "absolute",
                        top: "100%",
                        transform: "translate(-50%, -50%)",
                        left: "50%"
                      }}
                    >
                      <Spinner
                        as="span"
                        animation="border"
                        size="lg"
                        style={{}}
                        role="status"
                        aria-hidden="true"
                      />
                    </div>
                  ) : benefits && benefits?.results?.length > 0 ? (
                    benefits?.results.map(item => (
                      <>
                        {/* <Col lg="1" md="3" sm="6" className="p-1"></Col> */}
                        <Col lg="3" md="3" sm="4" className="p-1">
                            <div
                              className="cards"
                              // className="maincards"
                              style={{
                                // width: "220px",
                                backgroundColor: "white",
                                padding: 10,
                                borderRadius: 8,
                                //   boxShadow: "0px 4px 15px rgba(0, 0, 0, 0.25)",
                                border: "1px solid #E2E2E2",
                                maxWidth: "250px",
                                marginTop: 16
                              }}
                            >
                                <a
                                  target="_blank"
                                  rel="noopener noreferrer"
                                  style={{ textDecoration: "none" }}
                                  href={item.url}
                                >
                              <img
                                style={{
                                  marginRight: "20px",
                                  cursor: "pointer",
                                  height: "180px",
                                  borderRadius: 8,
                                  width: "100%"
                                }}
                                src={item.image}
                              />

                              {item.title === null ? (
                                <div style={{ height: 40 }}></div>
                              ) : (
                                <p
                                  style={{
                                    marginTop: 15,
                                    textAlign: "center",
                                    color: "#000000",
                                    fontSize: 16,
                                    fontFamily: "khula",
                                    fontWeight: "bold",
                                    lineHeight: "24px"
                                  }}
                                >
                                  {item.title}
                                </p>
                              )}
                             
                          </a>
                          <p
                                style={{
                                  marginTop: 15,
                                  textAlign: "center",
                                  color: "#000000",
                                  fontSize: 15,
                                  fontFamily: "khula",
                                  fontWeight: "500",
                                  lineHeight: "24px"
                                }}
                              >
                                <ReadMore
                                  length={30}
                                  item={item}
                                  text={item.details}
                                />
                               
                              </p>
                            </div>
                        </Col>
                      </>
                    ))
                  ) : (
                    <>
                      <div
                        style={{
                          position: "absolute",
                          top: "100%",
                          transform: "translate(-50%, -50%)",
                          left: "50%"
                        }}
                      >
                        <h6 style={{ textAlign: "center" }}>No Data Found</h6>
                      </div>
                    </>
                  )}
                </Row>
                <div className="mt-4 d-flex justify-content-center">
                  {benefits && benefits?.results?.length > 0 ? (
                    requesting ? null : (
                      <PaginationPack
                        onChange={handlePageChange}
                        itemsCountPerPage={benefits.page_size}
                        totalItemsCount={benefits?.total_objects}
                        activePage={benefits?.current_page_number}
                      />
                    )
                  ) : null}
                </div>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  )
}

const mapStateToProps = state => ({
  requesting: state.Benefits.requesting,
  benefits: state.Benefits.benefits
})

const mapDispatchToProps = dispatch => ({
  benefitsRequest: data => dispatch(benefitsRequest(data))
})
export default connect(mapStateToProps, mapDispatchToProps)(Benefits)
