async function getExpertiesDataAPI(data) {
  const URL = `${BASE_URL}/api/v1/experties/`
  const accessToken = await localStorage.getItem("authToken")

  const options = {
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      Authorization: `Token ${accessToken}`
    },
    method: "GET",
    data
  }

  return XHR(URL, options)
}

function* getExpertiesData({ data }) {
  try {
    const response = yield call(getExpertiesDataAPI, data)
    // console.log('getExperties success........', response);
    // yield put(
    //     push({
    //         pathname: '/admin/time_tracker'
    //     })
    // )
  } catch (e) {
    const { response } = e
    // console.log('getExperties failur........', response);
    // toast.error(`${response?.data?.approval_error[0]}`);
    // yield put(loginFaluire(response));
  }
}

