import {
  GET_BUSINESS_PROFILE,
  GET_BUSINESS_PROFILE_FAILURE,
  GET_BUSINESS_PROFILE_SUCCESS,
  PATCH_BUSINESS_PROFILE,
  PATCH_BUSINESS_PROFILE_FAILURE,
  PATCH_BUSINESS_PROFILE_SUCCESS
} from "./types"

const initialState = {
  requesting: false,
  error: false,
  BusinessProfileData: false,
  patchProfiledata : false
}

export default (state = initialState, action) => {
  switch (action.type) {
    case GET_BUSINESS_PROFILE:
      return {
        ...state,
        requesting: true
      }

    case GET_BUSINESS_PROFILE_SUCCESS:
      return {
        ...state,
        requesting: false,
        BusinessProfileData: action.data
      }

    case GET_BUSINESS_PROFILE_FAILURE:
      return {
        ...state,
        requesting: false,
        error: action.data
      }

    case PATCH_BUSINESS_PROFILE:
      return {
        ...state,
        requesting: true,
      }

    case PATCH_BUSINESS_PROFILE_SUCCESS:
      return {
        ...state,
        requesting: false,
        patchProfiledata: action.data,
      }

    case PATCH_BUSINESS_PROFILE_FAILURE:
      return {
        ...state,
        requesting: false,
        error: action.data
      }

    default:
      return state
  }
}
