import {
  POST_EDITPROFILE_DATA,
  POST_EDITPROFILE_DATA_SUCCESS,
  POST_EDITPROFILE_DATA_FAILURE,
  POST_USERROFILE_DATA,
  POST_USERROFILE_DATA_SUCCESS,
  POST_USERROFILE_DATA_FAILURE
} from "./types"
const initialState = {
  profileData: false,
  requesting: false,
  error: false,
}
export default (state = initialState, action) => {
  switch (action.type) {
    case POST_EDITPROFILE_DATA:
      return {
        ...state,
        requesting: true
      }

    case POST_EDITPROFILE_DATA_SUCCESS:
      return {
        ...state,
        requesting: false,
        error: false,
      }

    case POST_EDITPROFILE_DATA_FAILURE:
      return {
        ...state,
        requesting: false,
        error: action.data
      }

    case POST_USERROFILE_DATA:
      return {
        ...state,
        requesting: true
      }

    case POST_USERROFILE_DATA_SUCCESS:
      return {
        ...state,
        requesting: false,
        error: false,
      }

    case POST_USERROFILE_DATA_FAILURE:
      return {
        ...state,
        requesting: false,
        error: action.data
      }

    default:
      return state
  }
}
