import {
  GET_NOTIFICATION_DATA
  // GET_ENGAGEMENT_DATA_SUCCESS,
  // GET_ENGAGEMENT_DATA_FAILURE
} from "./types"
const initialState = {
  messageData: false,
  requesting: false,
  error: false
}
export default (state = initialState, action) => {
  switch (action.type) {
    case GET_NOTIFICATION_DATA:
      return {
        ...state,
        requesting: true,
        messageData: action.data
      }

    // case GET_ENGAGEMENT_DATA_SUCCESS:
    //   return {
    //     ...state,
    //     requesting: false,
    //     engagementData: action.data,

    //     error: false
    //   }

    // case GET_ENGAGEMENT_DATA_FAILURE:
    //   return {
    //     ...state,
    //     requesting: false,
    //     error: action.data
    //   }

    default:
      return state
  }
}
